import React, { useState } from 'react';
import { connect, useDispatch } from "react-redux";
import { Redirect, Link } from 'react-router-dom';
import './SignUp.css';
import Button from '@material-ui/core/Button';

import { registerUser, googleRegisterUser } from "../actions/";

const SignUp = ({ auth, history }) => {

    const dispatch = useDispatch();
    const [state, setState] = useState({
        fullName: '',
        email: '',
        password: '',
        toFrontpage: false,
        loading: false,
    });

    const onChange = (e) => {
        setState({
            ...state,
            [e.target.id]: e.target.value
        });
    };

    const handleSignUp = (e) => {
        setState({loading: true});
        e.preventDefault();
        dispatch(registerUser(state.email, state.password, state.fullName, () => history.push("/")));
        setState({loading: false});
    };

    const handleGoogleSignUp = (e) => {
        setState({loading: true});
        e.preventDefault();
        dispatch(googleRegisterUser(() => history.push("/")));
        setState({loading: false});
    }

    const redirect = (
        <Redirect to="/" />
    );

    const signUp = (
        <div className="trelloLogin">
            <img src="https://d2k1ftgv7pobq7.cloudfront.net/meta/c/p/res/images/trello-header-logos/76ceb1faa939ede03abacb6efacdde16/trello-logo-blue.svg"className="trelloLoginForm__logo" />

            <div class="trelloLoginFormContainer">
            <div className="trelloLoginForm">
                <h1>Signup to Trello</h1>
                <input type="name" className="trelloLoginForm__email" placeholder="Enter Full Name" id="fullName" onChange={onChange}/>

                <input type="email" className="trelloLoginForm__email" placeholder="Enter Email" id="email" onChange={onChange}/>
                
                <input type="password" className="trelloLoginForm__email" placeholder="Enter Password" id="password" onChange={onChange}/>
                
                <button type="submit" onClick={(e) => handleSignUp(e)} className="trelloLoginForm__button">{state.loading ? 'Loading...' : 'Sign Up'}</button>

                <h1 style={{margin:'10px', fontWeight: 400, fontSize: '13px'}}>OR</h1>
                
                <Button onClick={(e) => handleGoogleSignUp(e) } style={{width: '85%', boxShadow: '1px 1px 5px 5px rgba(0, 0, 0, 0.048)', marginBottom: '20px'}} variant="outlined" startIcon={<img src="https://d2k1ftgv7pobq7.cloudfront.net/meta/c/p/res/images/8215f6659adc202403198fef903a447e/sign-in-with-google.svg" />}>
                    Continue with Google
                </Button>
                <Link to="/signin"><p>Do not have an account ? Login</p></Link>
            </div>
            </div>
        </div>
    );

    if (state.toFrontpage) {
        return redirect;
    } else {
        return signUp;
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth
    };
}

export default connect(mapStateToProps)(SignUp);